export default "import React, {useEffect, useState} from 'react';\r\nimport {Button, OverlayTrigger, Popover} from 'react-bootstrap';\r\n\r\nconst UpdatingPopover = React.forwardRef(\r\n  ({popper, children, show: _, ...props}, ref) => {\r\n    useEffect(() => {\r\n      console.log('updating!');\r\n      popper.scheduleUpdate();\r\n    }, [children, popper]);\r\n\r\n    return (\r\n      <Popover ref={ref} body {...props}>\r\n        {children}\r\n      </Popover>\r\n    );\r\n  },\r\n);\r\n\r\nconst longContent = `\r\n  Very long\r\n  Multiline content\r\n  that is engaging and what-not\r\n`;\r\nconst shortContent = 'Short and sweet!';\r\n\r\nfunction Example() {\r\n  const [content, setContent] = useState(shortContent);\r\n\r\n  useEffect(() => {\r\n    const timerId = setInterval(() => {\r\n      setContent(content === shortContent ? longContent : shortContent);\r\n    }, 3000);\r\n\r\n    return () => clearInterval(timerId);\r\n  });\r\n\r\n  return (\r\n    <OverlayTrigger\r\n      trigger='click'\r\n      overlay={\r\n        <UpdatingPopover id='popover-contained'>{content}</UpdatingPopover>\r\n      }>\r\n      <Button>Holy guacamole!</Button>\r\n    </OverlayTrigger>\r\n  );\r\n}\r\n\r\nconst UpdatingPosition = () => {\r\n  return (\r\n    <div>\r\n      <Example />\r\n    </div>\r\n  );\r\n};\r\n\r\nexport default UpdatingPosition;\r\n";